@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css?family=Satisfy&display=swap);
@import url(https://fonts.googleapis.com/css?family=Dancing+Script&display=swap);
@import url(https://fonts.googleapis.com/css?family=Dancing+Script|Josefin+Sans|Satisfy&display=swap);
@import url(https://fonts.googleapis.com/css?family=Dancing+Script|Josefin+Sans|Satisfy&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*********** all  **********/

.App {
  text-align: center;
  color: black;
  /* background-size: cover;
  background-image:url("./assets/sunset-switzerland.png"); */
  background-color: #E0EBE8;
  font-family: 'Josefin Sans', sans-serif;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  min-height: 100vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: contain;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.underline{
  -webkit-animation: blink infinite 1s linear;
          animation: blink infinite 1s linear;
}

@-webkit-keyframes blink {
  0%   {color:#E0EBE8; }
  49%   {color:#E0EBE8; }
  50%  {color:black; }
  100%  {color:black; }
  }

@keyframes blink {
  0%   {color:#E0EBE8; }
  49%   {color:#E0EBE8; }
  50%  {color:black; }
  100%  {color:black; }
  }

  .Pagination{
    width: 99vw;
    display: flex;
    justify-content: space-between;
    position: absolute;
    bottom: 0;
  }

  .pagination-arrow{
    color: #9EC6C0;
  }
  .pagination-arrow-a{
    margin: 7%;
    position: absolute;
    bottom: 0;
    border: none;
    background: none;
  }
  .pagination-arrow-a:focus{
    outline: none;
    border: none;
  }
  .pagination-arrow-a-right{
    right: 0;
  }
  .pagination-arrow-a-left{
    left: 0;
  }

  .sun{
    width: 14%;
    position: absolute;
  }

  .cloude{
    width: 10%;
    position: absolute;
  }

  .windmill-container{
    position: absolute;
    bottom: 0;
  }
  .windmill-vector{
    bottom: 0;
    position: absolute;
    z-index: 0;
  }
  .windmill-spin{
    position: absolute;
    z-index: 1;
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
  /*********** desktop  **********/
  @media only screen and (min-width: 800px) {
    .App-header{
      background-image: url(/NogaAnabyCV/static/media/switzerland-with-the-sun-low.746f8dee.png);
    }
    .windmill-vector{
      max-width: 100px;
      left: 77px;
    }
    .windmill-spin{
      max-width: 250px;
      bottom: 54px;
    }
  }

/*********** mobile  **********/
  @media only screen and (max-width: 800px) {
    .App-header{
      background-image: url(/NogaAnabyCV/static/media/switzerland-mobile.aa8f0bdd.png);
    }
    .windmill-vector{
      max-width: 50px;
      left: 39px;
    }
    .windmill-spin{
      max-width: 125px;
      bottom: 30px;
    }
  }

